
import FormPreview from 'vux/src/components/form-preview/index.vue'

import moment from 'moment'
export default {
components: {
FormPreview
},
data () {
return {
list:[],//所有排课
info:[],//页面显示信息
buttons: [{
style: 'default',
text: '详情',

onButtonClick: (name) => {
console.log(name)
this.$router.push({

path: '/homework-check',
query: name
});
}
}],//作业操作
}
},
created(){
this.getList()
console.log(this.$route)
},
methods:{
getList() {
console.log(this.$store.getters)
this.$http.get('http://139.199.168.158:8080/admin/teacher_homework?id='+this.$store.getters.userid).then((response) => {
response = response.data
console.log(response)
if (response.code === 1) {
console.log('获取作业成功')
for(var i = 0 ; i < response.data.length ; i++){
let temp={
date:moment(response.data[i].SUBMISSION_DATE).format('YYYY-MM-DD'),
studentname:response.data[i].STUDENT_NAME,
param:{
value:response.data[i].id
}
}
this.info.push(
[
{
label:'ID：',
value:response.data[i].HOMEWORK_ID
},
{
label:'课程：',
value:response.data[i].SUBJECT_NAME
},

])
this.list.push(temp)
}
}
else{
console.log('获取作业失败')
}
})
},
}
}



